}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Asap", "Arial", sans-serif;
  color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}
@media (max-width: 1439px) {
  html {
    font-size: 0.8333333333vw;
  }
}

body {
  width: 100%;
  height: 100%;
  background-color: #151515;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #151515;
  -webkit-text-fill-color: #ffffff;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 64px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 40px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.container-more-less {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 130px;
}
@media (max-width: 1439px) {
  .container-more-less {
    padding: 0 40px;
  }
}
@media (max-width: 1023px) {
  .container-more-less {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container-more-less {
    padding: 0 16px;
  }
}

.header {
  width: 100%;
  padding: 20px 0;
  background-color: #151515;
}
@media (max-width: 767px) {
  .header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 30;
  }
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__wrapper {
  display: contents;
}
@media (max-width: 767px) {
  .header__wrapper {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 64px;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 5;
    padding: 40px;
    padding-bottom: 60px;
    background-color: #151515;
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    overflow: auto;
  }
  .is-open .header__wrapper {
    opacity: 1;
    pointer-events: all;
    visibility: visible;
  }
  .header__wrapper .header__link-enter {
    transition: opacity 0.3s ease, transform 0.3s ease;
    opacity: 0;
    transform: translateY(25px);
  }
  .is-open .header__wrapper .header__link-enter {
    opacity: 1;
    transform: translateY(0);
  }
}
.header__wrapper a {
  color: #D9D9D9;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header__wrapper a:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.header__wrapper a.focus-visible:focus {
  opacity: 0.7;
}
.header__nav {
  margin: 0 40px;
}
@media (max-width: 767px) {
  .header__nav {
    margin: 0 auto;
  }
  .header__nav .header__item {
    transition: opacity 0.3s ease, transform 0.3s ease;
    opacity: 0;
    transform: translateY(-25px);
  }
  .is-open .header__nav .header__item {
    opacity: 1;
    transform: translateY(0);
  }
}
.header__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  column-gap: 40px;
  row-gap: 10px;
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 16px;
  line-height: 114%;
}
@media (max-width: 767px) {
  .header__list {
    flex-direction: column;
    gap: 20px;
  }
}
.header__link.is-active {
  position: relative;
}
.header__link.is-active::before {
  content: "";
  position: absolute;
  top: -8px;
  left: -8px;
  width: calc(100% + 16px);
  height: calc(100% + 16px);
  border: 1px solid #D9D9D9;
  border-radius: 16px;
}
.header__link-enter {
  font-size: 16px;
  line-height: 114%;
}
@media (max-width: 767px) {
  .header__link-enter {
    margin: auto auto 0 auto;
  }
}
.header__burger {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;
  background-color: rgba(255, 255, 255, 0);
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .header__burger:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.header__burger.focus-visible:focus {
  opacity: 0.7;
}
.header__burger span {
  width: 24px;
  height: 2px;
  background-color: #ffffff;
  transition: transform 0.6s ease, opacity 0.3s ease, background-color 0.3s ease;
}
.is-open .header__burger span:nth-child(1) {
  transform: translateY(8px) rotate(225deg);
}
.is-open .header__burger span:nth-child(3) {
  transform: translateY(-8px) rotate(-225deg);
}
.is-open .header__burger span:nth-child(2) {
  opacity: 0;
}
@media (max-width: 767px) {
  .header__burger {
    display: flex;
  }
}

@media (max-width: 767px) {
  .header--shadow {
    box-shadow: 1px 2px 4px 0 rgba(255, 255, 255, 0.2), 0 0 0 4px rgba(21, 21, 21, 0.6);
  }
}

.footer {
  padding: 40px 0 113px 0;
}
@media (max-width: 767px) {
  .footer {
    padding: 20px 0 50px 0;
  }
}
.footer .container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.footer .logo {
  margin-top: 6px;
}
@media (max-width: 767px) {
  .footer .logo {
    margin-top: 3px;
  }
}
.footer__info {
  display: flex;
  flex-direction: column;
  gap: 8px;
  min-width: 199px;
  font-size: 24px;
  font-weight: 700;
  line-height: 114%;
}
@media (max-width: 767px) {
  .footer__info {
    min-width: 130px;
    font-size: 16px;
    font-weight: 500;
  }
}
.footer__info a {
  color: #ffffff;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__info a:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.footer__info a.focus-visible:focus {
  opacity: 0.7;
}

.btn {
  display: inline-flex;
  padding: 12px 20px;
  border: none;
  border-radius: 16px;
  background-color: #E2FF32;
  color: #151515;
  font-size: 16px;
  font-weight: 600;
  line-height: 114%;
  text-transform: uppercase;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (max-width: 767px) {
  .btn {
    padding: 10px 16px;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    line-height: 114%;
  }
}
.btn--form {
  padding: 20px 40px;
  font-size: 20px;
  font-weight: 600;
  line-height: 114%;
  text-transform: capitalize;
}
@media (max-width: 767px) {
  .btn--form {
    padding: 10px 16px;
    font-size: 16px;
    line-height: 114%;
    text-transform: uppercase;
  }
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.7;
  }
}
.btn.focus-visible:focus {
  opacity: 0.7;
}

.custom-input,
.custom-textarea {
  position: relative;
  display: flex;
  flex-grow: 1;
}
.custom-input label,
.custom-textarea label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .custom-input label,
.custom-textarea label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
  flex-grow: 1;
  min-height: 62px;
  padding: 18px;
  border-radius: 16px;
  border: 2px solid #151515;
  outline: none;
  transition: border 0.3s ease;
  background-color: #151515;
  font-size: 20px;
  font-weight: 400;
  line-height: 115%;
  color: #ffffff;
}
.custom-input label input::placeholder,
.custom-input label textarea::placeholder,
.custom-textarea label input::placeholder,
.custom-textarea label textarea::placeholder {
  color: #ACACAC;
}
@media screen and (max-width: 767px) {
  .custom-input label input,
.custom-input label textarea,
.custom-textarea label input,
.custom-textarea label textarea {
    max-width: unset;
    width: 100%;
    min-height: 42px;
    padding: 10px;
    border-radius: 8px;
    transition: border 0.3s ease;
    background-color: #151515;
    font-size: 16px;
  }
}
.custom-input label input:hover,
.custom-input label textarea:hover,
.custom-textarea label input:hover,
.custom-textarea label textarea:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-input label input:focus,
.custom-input label textarea:focus,
.custom-textarea label input:focus,
.custom-textarea label textarea:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
.custom-input__error,
.custom-textarea__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media screen and (max-width: 767px) {
  .custom-input__error,
.custom-textarea__error {
    bottom: 20px;
  }
}
.custom-input.is-invalid input,
.custom-input.is-invalid textarea,
.custom-textarea.is-invalid input,
.custom-textarea.is-invalid textarea {
  border: 2px solid #ff1553;
}
.custom-input.is-invalid input:focus, .custom-input.is-invalid input:hover,
.custom-input.is-invalid textarea:focus,
.custom-input.is-invalid textarea:hover,
.custom-textarea.is-invalid input:focus,
.custom-textarea.is-invalid input:hover,
.custom-textarea.is-invalid textarea:focus,
.custom-textarea.is-invalid textarea:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-input.is-valid input,
.custom-textarea.is-valid input {
  border: 2px solid #22b259;
}
.custom-input.is-valid input:focus, .custom-input.is-valid input:hover,
.custom-textarea.is-valid input:focus,
.custom-textarea.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.custom-input .input-message,
.custom-textarea .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-input .input-message.is-invalid,
.custom-textarea .input-message.is-invalid {
  color: #ff0000;
}
.custom-input .input-message.is-valid,
.custom-textarea .input-message.is-valid {
  color: #15ab07;
}

.custom-toggle {
  position: relative;
  display: flex;
  width: 245px;
  font-size: 16px;
  font-weight: 500;
  line-height: 18.34px;
  color: #ACACAC;
}
@media screen and (max-width: 767px) {
  .custom-toggle {
    width: auto;
    font-size: 14px;
  }
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  padding-left: 29px;
  cursor: pointer;
}
.custom-toggle label span {
  margin-bottom: 0;
  margin-left: 0;
}
.custom-toggle--checkbox .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
  border-radius: 2px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .custom-toggle--checkbox .custom-toggle__icon {
    top: 2px;
  }
}
.custom-toggle--checkbox .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 12px;
  height: 6px;
  border-left: 2px solid #151515;
  border-bottom: 2px solid #151515;
  transform: translate(-50%, -70%) rotate(-45deg);
  opacity: 0;
  transition: opacity 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1px solid #2c39f2;
  border-radius: 50%;
  transition: border 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
