.circle9 {
  transition-delay: calc(9 * var(--delay));
}

.circle10 {
  transition-delay: calc(10 * var(--delay));
}

.circle11 {
  transition-delay: calc(11 * var(--delay));
}

.circle12 {
  transition-delay: calc(12 * var(--delay));
}

.in {
  opacity: 0;
  transform: translateY(-150px);
  transition: opacity 0.45s ease-in 1s, transform 0.45s ease-in 1s;
}

.pocket {
  opacity: 0;
  transform: perspective(180px) rotateY(-90deg);
  transition: transform 0.45s ease-in 0.9s;
  transform-origin: center;
}

.play .pocket {
  opacity: 1;
  transform: rotateY(0);
}
.play .circle1,
.play .circle2,
.play .circle3,
.play .circle4,
.play .circle5,
.play .circle6,
.play .circle7,
.play .circle8,
.play .circle9,
.play .circle10,
.play .circle11,
.play .circle12 {
  opacity: 1;
}
.play .in {
  opacity: 1;
  transform: rotateY(0);
}